.bar {
    display: flex;
    width: inherit;
    height: 50px;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: flex-end;
    padding: 10px;
    background-color: var(--primary-color);

    &_btn {
        display: inline-flex;
        color: #fff;
        margin-bottom: 0;
        padding: 8px 15px;
        justify-content: center;
        align-items: center;
        font-size: 22px;
        font-weight: bold;
        border-radius: 5px;
        cursor: pointer;
        user-select: none;
    }
}

@import './media.scss';