.btn {
    width: 180px;
    height: 45px;
    color: var(--text-interface);
    background-color: var(--primary-color);
    font-size: 16px;
    border: none;
    border-radius: 5px;
    cursor: pointer;

    &-small {
        width:max-content;
    }

    &:hover {
        opacity: .90;
    }

    &-wrapper {
        display: flex;
        flex-direction: row;
        align-items: center;
    }

    &-container {
        display: inline-flex;
        width: max-content;
        justify-content: space-between;
        align-items: center;
        padding: 15px;
        padding-right: 0;
    }

    &-standby {
        position: absolute;
        width: 35px;
        height: 35px;
        display: flex;
        justify-content: center;
        align-items: center;
        border: none;
    }
}

#encryptionKey {
    margin: 2px;
}

#clipboard, #clear, #resetVal {
    right: 4px;
    bottom: 5px;
}

#pasteFromClipboard {
    right: 5px;
    bottom: 10px;
}

#copyVal {
    right: 20px;
    bottom: 21px;
}
  