.field-wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.decryptionLeftFieldsWrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.field {
    display: inline-block;
    height: 350px;
    padding: 20px;
    padding-right: 22px;
    border: solid 1px var(--primary-color);
    border-radius: 5px;
    color: var(--primary-color);
    font-weight: 300;
    font-size: 18px;
    background-color: transparent;

    scrollbar-color: var(--primary-color);
    scrollbar-width: thin;
    
    resize: none;

    &-disabled {
        background-color: var(--disabled-field);
    }

    &:focus {
        outline: solid 3px #4b0082;
        border: none;
    }

    &::placeholder{
        font-weight: 300;
        font-style: italic;
        letter-spacing: .1rem;
        color: #444
    }
}

#inputDecr {
    height: 285px;
}

#key{
    height: 15px;
    margin-top: 4px;
    padding-right: 45px;
}
