@import 'styles/bar/bar.scss';
@import 'styles/btns/btns.scss';
@import 'styles/textfields/textfields.scss';
@import 'styles/container/container.scss';

@import 'styles/container/media.scss';
@import 'styles/bar/media.scss';
@import 'styles/btns/media.scss';
@import 'styles/textFields/media.scss';

:root {
    --primary-color : #5d0099;
    --disabled-field: #dcd0ff;
    --text-interface: #fff;
    --body-background: #e8dfff;
}

body {
    background-color: var(--body-background);
}

ul {
    font-size: 20px;

    li {
        margin-top: 15px;
    }
}

@media (max-width: 575px) {
    ul {
        padding: 20px;
        font-size: 16px;
    }
}

#instruction {
    padding: 20px;
}
